// GLOBAL DOCUMENT | Interactive elements custom styling

// Input Text
.custom__input-text {
  padding: 18px;
  font: $f_reg18;
  color: $c_darkBlue1;
  border: 2px solid $c_darkGray1;
  border-radius: 3px;

  &:hover {
    border-color: $c_darkBlue1;
  }

  &:focus,
  &:active {
    border-color: $c_red;
    outline: none;
  }

  &:disabled {
    color: $c_darkGray2;
    border: 2px solid $c_darkGray2;
  }
}

// Text Area
.custom__textarea {
  font: $f_reg18;
  color: $c_darkBlue1;
  border: 2px solid $c_darkGray1;
  border-radius: 3px;
  resize: none;

  &:hover {
    border-color: $c_darkBlue1;
  }

  &:focus,
  &:active {
    border-color: $c_red;
    outline: none;
  }

  &:disabled {
    color: $c_darkGray2;
    border: 2px solid $c_darkGray2;
  }
}

// Input checkbox
.custom__checkbox {
  display: block;
  width: 39px;
  height: 39px;
  border: 2px solid $c_darkGray1;
  border-radius: 3px;
  background-color: $c_white;
}

.custom__input[type=checkbox]:hover ~ .custom__label .custom__checkbox {
  border-color: $c_darkBlue1;
}

.custom__input[type=checkbox]:focus ~ .custom__label .custom__checkbox,
.custom__input[type=checkbox]:active ~ .custom__label .custom__checkbox {
  border-color: $c_red;
  outline: none;
}

.custom__input[type=checkbox]:disabled ~ .custom__label .custom__checkbox {
  border: 2px solid $c_darkGray2;
}

.custom__input[type=checkbox]:checked ~ .custom__label .custom__checkbox {
  background: url('../../img/icon-yes-checkbox.svg') no-repeat;
  background-position: 50%;
}

// Input radio
.custom__radio {
  display: block;
  width: 39px;
  height: 39px;
  padding: 10px 10px;
  border: 2px solid $c_darkGray1;
  border-radius: 50%;
  background-color: $c_white;
}

.custom__input[type=radio]:hover ~ .custom__label .custom__radio {
  border-color: $c_darkBlue1;
}

.custom__input[type=radio]:focus ~ .custom__label .custom__radio,
.custom__input[type=radio]:active ~ .custom__label .custom__radio {
  border-color: $c_red;
  outline: none;
}

.custom__input[type=radio]:disabled ~ .custom__label .custom__radio {
  border: 2px solid $c_darkGray2;
}

.custom__input[type=radio]:checked ~ .custom__label .custom__radio:before {
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  background-color: $c_red;
  border-radius: 50%;
}

// Input range
.custom__input-range::-ms-fill-lower {
  appearance: none;
  height: 2px;
  background-color: $c_black;
  border: none;
  border-top: 8px solid $c_gray;
  border-bottom: 8px solid $c_gray;
}

.custom__input-range::-ms-fill-upper {
  appearance: none;
  height: 2px;
  background-color: $c_black;
  border: none;
  border-top: 8px solid $c_gray;
  border-bottom: 8px solid $c_gray;
}

.custom__input-range::-ms-thumb {
  appearance: none;
  width: 10px;
  height: 10px;
  margin-top: 2px;
  background-color: $c_white;
  border: 2px solid $c_red;
  border-radius: 50%;

  &:focus {
    outline: none;
    background-color: $c_red;
  }

  &:active {
    background-color: $c_red;
  }
}

.custom__input-range::-ms-track {
  appearance: none;
  height: 0px;
  color: transparent;
  background-color: $c_black;
  border: none;
  border-top: 8px solid $c_gray;
  border-bottom: 8px solid $c_gray;
}

.custom__input-range {
  -webkit-appearance: none;
  padding: 0;
  background-color: transparent;
}

.custom__input-range::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 2px;
  background-color: $c_black;

  &:focus {
    outline: none;
  }
}

.custom__input-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 14px;
  height: 14px;
  margin-top: -6px;
  background-color: $c_white;
  border: 2px solid $c_red;
  border-radius: 50%;

  &:focus {
    background-color: $c_red;
    outline: none;
  }

  &:active {
    background-color: $c_red;
  }
}

.custom__input-range::-moz-range-track {
  appearance: none;
  height: 2px;
  padding: 0;
  background-color: $c_black;
  border: none;
  outline: none;

  &:focus {
    outline: none;
  }
}

.custom__input-range::-moz-range-thumb {
  appearance: none;
  width: 14px;
  height: 14px;
  background-color: $c_white;
  border-color: transparent;
  color: transparent;
  border: 2px solid $c_red;
  border-radius: 50%;

  &:focus {
    background-color: $c_red;
    outline: none;
  }

  &:active {
    background-color: $c_red;
  }
}

.custom__input-range::-moz-focus-outer {
  border: none;
}

// Slider
.custom__slider-bullets {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.custom__slider-bullets-item:not(:last-child) {
  margin-right: 19px;
}

// Horizontal line
.custom__hr {
  border: 1px solid $c_darkGray1;
}







