// GLOBAL DOCUMENT

// Form factor
$ff_small: 320px;
$ff_until-medium: 659px;
$ff_medium: 660px;
$ff_until-large: 959px;
$ff_large: 960px;

// Device pixel ratios
// 1dppx = 96dpi
$ratio1_dpi: 96dpi;
$ratio2_dpi: 192dpi;

// Alignment
$align_small: 320px;
$align_small-fields: calc(#{$align_small} - 40px);
$align_medium: 660px;
$align_medium-fields: calc(#{$align_medium} - 20px);
$align_large: 960px;
$align_large-fields: calc(#{$align_large} - 20px);

// Colors
$c_white: #ffffff;
$c_black: #000000;
$c_darkBlue1: #283645;
